import React from 'react'
import Carousel, { Dots } from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';

import { Img } from "gatsby-image";

import { useStaticQuery, graphql } from "gatsby"

import BackgroundSlider from 'gatsby-image-background-slider'

// import { Carousel } from 'react-bootstrap';

import img1 from "../images/slider/harli.jpg";
import img2 from "../images/slider/jos.jpg";
import img3 from "../images/slider/kal.jpg";
import img4 from "../images/slider/oci.jpg";
import img5 from "../images/slider/zubi.jpg";
import Image1 from "../components/image1"


const styles = {
    margin: '10px'
}


const CarouselSmall = ({ props }) => {

   return <Carousel 
                    clickToChange
                    slidesPerPage={4}
                    infinite
                    keepDirectionWhenDragging
                    autoPlay={4000}
                    animationSpeed={1000}
                    offset={10}>
                <img src={img1} />
                <img src={img2} />
                <img src={img3} />
                <img src={img4} />
                <img src={img5} />
 
        </Carousel>

}

export default CarouselSmall;

